<template>
  <div class="g-usercenter">
    <div class="m-usermsg">
      <div class="s-img"><img :src="user.pic||'../assets/images/head.png'" alt=""></div>
      <div class="s-msgDetails">
        <span class="name">{{user.username}}</span>
        <span class="level" v-if="user.vip!=0">{{user.vip==1?'普通会员':user.vip==2?'vip会员':'超级VIP会员'}}</span>
      </div>
      <div class="s-money">
        <div class="jf"><span>我的积分</span><b>{{user.accountBalance}}</b></div>
        <div class="yb"><span>我的元宝</span><b>{{user.ingots}}</b></div>
      </div>
    </div>
    <div class="m-navList">
      <router-link :to="{name:'order'}" tag="div" class="s-box">
        <div>
          <i>
            <img src="../assets/images/dd.png" alt="">
          </i>
          <span>我的订单</span>
        </div>
      </router-link>
      <router-link :to="{name:'bill'}" tag="div" class="s-box">
        <div>
          <i>
            <img src="../assets/images/xf.png" alt="">
          </i>
          <span>消费明细</span>
        </div>
      </router-link>
      <router-link :to="{name:'extension'}" tag="div" class="s-box">
        <div>
          <i>
            <img src="../assets/images/tg.png" alt="">
          </i>
          <span>推广中心</span>
        </div>
      </router-link>
      <router-link :to="{name:'addressList'}" tag="div" class="s-box">
        <div>
          <i>
            <img src="../assets/images/sh.png" alt="">
          </i>
          <span>收货地址</span>
        </div>
      </router-link>
      <router-link v-if="user.vip==2||user.vip==3" :to="{name:'apply'}" tag="div" class="s-box">
        <div>
          <i>
            <img src="../assets/images/lm.png" alt="">
          </i>
          <span>联盟商家申请</span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "g-usercenter",
  data(){
    return {
      user:''
    }
  },
  created(){
    this.userMsg();//用户信息
  },
  methods:{
    userMsg(){
      this.$post(this.$bee+'/api/user/userDetail').then((data)=>{
        if(data.code==200){
          this.user=data.data.user;
        }else{
          this.$createToast({
            txt: data.message,
            type: 'txt'
          }).show();
        }
      })
    },
  }
};
</script>

<style scoped lang="stylus">
.m-usermsg
  height 150px
  background-image linear-gradient(97deg,#ec6716 0%,#ebc823 100%),linear-gradient(#d53236,#d53236)
  background-blend-mode normal,normal
  padding 20px 25px 0 25px
  box-sizing border-box
  position relative
  .s-img
    width 58px
    height 58px
    float: left
    img
      width 100%
      height 100%
      border-radius 50%
  .s-msgDetails
    float left
    text-align left
    padding-left 10px
    span
      display block
      color: #ffffff
      font-size 15px
      &.name
        margin-top 12px
      &.level
        font-size 12px
        width auto
        padding 0 5px
        height 18px
        line-height 18px
        border-radius 8px
        background #f4b976
        text-align center
        margin-top 8px
  .s-money
    display flex
    position absolute
    bottom 0
    left calc(50% - 175px)
    div
      text-align center
      padding-top 8px
      flex 1
      display block
      width 175px
      padding-bottom 8px
      background-color: rgba(20,20,20,0.3)
      &.jf
        border-right 1px solid #fff
      span,b
        display block
        color #fff
      span
        font-size 15px
      b
        font-size 17px
        margin-top 8px
.m-navList
  .s-box
    height 50px
    line-height 50px
    text-align left
    background url(../assets/images/right-arrow.png) no-repeat center right
    background-size 7.5px 14px
    background-position calc(100% - 18px) center
    padding 0 12px
    box-sizing border-box
    div
      border-bottom 1px solid #d0d0d0
    span
      font-size 16px
      color #585858
      margin-left 10px
    i
      display inline-block
      width 18px
      height 18px
      vertical-align middle
      img
        width 100%
        height 100%
        vertical-align top
</style>
